export default "@import \"https://fonts.googleapis.com/css?family=Lato:300,400,500,600,700\";\n/* ---------------------------------------------------\n    SIDEBAR STYLE\n----------------------------------------------------- */\n#sidebar {\n  width: 250px;\n  position: fixed;\n  top: 0;\n  left: 0;\n  height: 100vh;\n  z-index: 999;\n  background: #fff;\n  color: #000;\n  transition: all 0.3s;\n  border-right: 1px solid #8e939b;\n}\n#sidebar.active {\n  margin-left: -250px;\n}\n#sidebar .sidebar-header {\n  padding: 20px;\n  background: #ffffff;\n  color: #000;\n}\n.sidebar-header h6 {\n  margin-left: 3px;\n}\n.sidebar-header .fa {\n  float: right;\n  margin-top: -27px;\n}\n#sidebar ul li a {\n  padding: 10px;\n  font-size: 1.1em;\n  display: block;\n}\n#sidebar ul li a:hover {\n  text-decoration: none;\n}\n.list-data li {\n  line-height: 11px;\n}\n.list-data, .list-input {\n  margin-top: 27px;\n  margin-left: -23px;\n}\n.list-input li {\n  line-height: 15px;\n  padding: 7px 9px;\n  font-size: 12px;\n}\n.list-input li a {\n  margin-left: -9px;\n}\n.list-input li input[type=text] {\n  width: 88%;\n}\n.list {\n  font-family: Lato;\n  font-size: 11px;\n  color: black;\n  list-style-type: none;\n}\n.list a {\n  color: black !important;\n}\n.list a:hover {\n  color: #007bff !important;\n}\nul.CTAs {\n  padding: 20px;\n}\nul.CTAs a {\n  text-align: center;\n  font-size: 0.9em !important;\n  display: block;\n  border-radius: 5px;\n  margin-bottom: 5px;\n}\nli a.addnew {\n  background: #373d47;\n  color: #fff;\n  border-radius: 4px;\n  width: 90%;\n  cursor: pointer;\n}\n.btn-group {\n  position: relative;\n  display: inline-flex;\n  /* vertical-align: middle; */\n  width: 88%;\n  margin-left: -15px;\n  height: 25px;\n  background: white;\n}\n.btn-group .btn {\n  position: relative;\n  flex: 1 1 auto;\n  background: white;\n  color: black;\n  padding: 0px;\n  font-size: 13px;\n  font-family: Lato;\n  cursor: pointer;\n}\n.dropdown-item {\n  display: block;\n  width: 100%;\n  font-size: 13px;\n  padding: 0.25rem 1.5rem;\n  clear: both;\n  font-weight: 400;\n  color: #212529;\n  text-align: inherit;\n  white-space: nowrap;\n  background-color: transparent;\n  border: 0;\n}\n.dropdown-menu {\n  width: 100%;\n}\n/* ---------------------------------------------------\n    CONTENT STYLE\n----------------------------------------------------- */\n#content {\n  width: calc(100% - 250px);\n  padding: 40px;\n  min-height: 100vh;\n  transition: all 0.3s;\n  position: absolute;\n  top: 0;\n  right: 0;\n}\n#content.active {\n  width: 100%;\n}\n.page-content {\n  width: calc(100vw - 250px);\n  float: right;\n  height: 100vh;\n  padding: 1% 2%;\n}\n/* ---------------------------------------------------\n    MEDIAQUERIES\n----------------------------------------------------- */\n@media (max-width: 768px) {\n  #sidebar {\n    margin-left: -250px;\n  }\n\n  #sidebar.active {\n    margin-left: 0;\n  }\n\n  #content {\n    width: 100%;\n  }\n\n  #content.active {\n    width: calc(100% - 250px);\n  }\n\n  #sidebarCollapse span {\n    display: none;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"